import React, {Fragment} from 'react';
import {connect} from "react-redux";
import {setChatState, setChatId} from "../../../../redux/app/app.actions";
import {getDate} from "../../../../redux/Utils";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell/TableCell";
import TableBody from "@material-ui/core/TableBody";
import {IconPack} from "../../../../Images/IconPack";
import TextField from "@material-ui/core/TextField/TextField";

const Transactions = (props) => {
    const [searchValue, setSearchValue] = React.useState();
    const tableAction = (rowData) => {
        return (<div className="tableAction" onClick={()=>{tableActionHendler(rowData)}}><svg width="5" height="14" viewBox="0 0 5 14" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M2.5 5.29688C1.48438 5.29688 0.671875 6.13477 0.671875 7.125C0.671875 8.14062 1.48438 8.95312 2.5 8.95312C3.49023 8.95312 4.32812 8.14062 4.32812 7.125C4.32812 6.13477 3.49023 5.29688 2.5 5.29688ZM0.671875 2.65625C0.671875 3.67188 1.48438 4.48438 2.5 4.48438C3.49023 4.48438 4.32812 3.67188 4.32812 2.65625C4.32812 1.66602 3.49023 0.828125 2.5 0.828125C1.48438 0.828125 0.671875 1.66602 0.671875 2.65625ZM0.671875 11.5938C0.671875 12.6094 1.48438 13.4219 2.5 13.4219C3.49023 13.4219 4.32812 12.6094 4.32812 11.5938C4.32812 10.6035 3.49023 9.76562 2.5 9.76562C1.48438 9.76562 0.671875 10.6035 0.671875 11.5938Z" fill="white" fillOpacity="0.6"/>
        </svg></div>);
    };

    const tableActionHendler = (data) => {
        // open popup
        // insert data
    }


    let rows =  (<>
        <TableRow>
            <TableCell className="padding-sides-20 red text-align-right">-150.00</TableCell>
            <TableCell className="padding-sides-25 ">
                <span className="icon-inside color-white align-items-baseline">{IconPack.operations.statusGood}Completed</span>
            </TableCell>
            <TableCell className="padding-sides-20 color-white">Bitcoin</TableCell>
            <TableCell className="padding-sides-15 color-white ">Top up balance</TableCell>
            <TableCell className="padding-sides-15"><span className="icon-inside icon-inside--right color-white align-items-baseline ">154895327595{IconPack.operations.id}</span></TableCell>
            <TableCell className="padding-sides-20 ">3 Apr, 18:54:45</TableCell>
        </TableRow>

    </>);




    /* active */
    return (
        <div className="сustomStyle">
            <div className="filterPanel">
                <TextField
                    variant="filled"
                    fullWidth
                    className="search_area"
                    placeholder="Search"
                    value={searchValue}
                    onChange={(e)=>{setSearchValue(e.target.value)}}
                >


                </TextField>
                <div className="search_additional_components">
                    <div className="calendar">{IconPack.searchComponents.calendar}</div>
                    <div>{IconPack.searchComponents.filter}</div>
                </div>
            </div>
            <Table>
                <TableHead>
                    <TableRow>
                        <TableCell className="padding-sides-20 w100 text-align-right">USD</TableCell>
                        <TableCell className="padding-sides-25 w175">Status</TableCell>
                        <TableCell className="padding-sides-20 w150">Payment system</TableCell>
                        <TableCell className="padding-sides-15  ">Description</TableCell>
                        <TableCell className="padding-sides-15 w150 ">Transaction ID</TableCell>
                        <TableCell className="padding-sides-20 w125">Date</TableCell>
                    </TableRow>
                </TableHead>
                <TableBody>
                    {rows}
                </TableBody>
            </Table>
        </div>
    );
}
const mapStateToProps = (state) => ({
    deals: state.deals.deals,
    appState: state.appOptions.appState
});

const mapDispatchToProps = (dispatch) => {
    return {
        setChatState: (bool) => dispatch(setChatState(bool)),
        setChatId: (numb) => dispatch(setChatId(numb)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Transactions);