import React from 'react';
import TextField from "@material-ui/core/TextField/TextField";
import {connect} from "react-redux";
import Button from "@material-ui/core/Button";
import {setResetPassAuthModal, setSignInAuthModal, setSignUpAuthModal, setChangePasswordAuthModal} from "../../redux/app/app.actions";

const ResetPass = (props) => {

    const [login, setLogin] = React.useState();

    const cancelHandler = () => {
        if(props.appState.isUserLoggedIn){
            props.setResetPassAuthModal(false);
        }else {
            props.setResetPassAuthModal(false);
            props.setSignInAuthModal(true);
        }
    };
    const loginHandler = () => {

    };

    return (
        <div className="authModal w400">
            <header>
                <p>Reset password</p>
            </header>
            <main>
                <TextField
                    variant="filled"
                    fullWidth
                    label="Youre login"
                    value={login}
                    onChange={(e)=>{setLogin(e.target.value)}}>
                </TextField>

            </main>
            <footer>
                <p>We will send a password reset link <span className="color-white">to your email</span> account associated with the specified login</p>
                <Button variant="contained" className="btn btn--blue" onClick={()=>{
                    props.setResetPassAuthModal(false);
                    props.setChangePasswordAuthModal(true);
                }}>
                    Send reset password link
                </Button>
                <div className="btn btn--text" onClick={()=>{cancelHandler()}}>Cancel change password</div>
            </footer>
        </div>
    );
};

const mapStateToProps = (state) => ({
    appState: state.appOptions.appState,
});

const mapDispatchToProps = (dispatch) => {
    return {
        setSignInAuthModal: (bool) => dispatch(setSignInAuthModal(bool)),
        setSignUpAuthModal: (bool) => dispatch(setSignUpAuthModal(bool)),
        setChangePasswordAuthModal: (bool) => dispatch(setChangePasswordAuthModal(bool)),
        setResetPassAuthModal: (bool) => dispatch(setResetPassAuthModal(bool)),
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(ResetPass);