let usersTabClone, currentUserObj;

const sortTable = (arr, type) => {
    if(type === 'byRegistration'){
        arr.sort((a, b) => a.user.dateOfRegister > b.user.dateOfRegister  ? -1 : 1);
    }
    if(type === 'byVisit'){
        arr.sort((a, b) => a.user.dateOfVisit > b.user.dateOfVisit  ? -1 : 1);
    }

}

const usersReducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case usersTypes.SET_COSTS:

            usersTabClone = [...state.usersTab];

            currentUserObj = usersTabClone.find((userObject) => {
                return userObject.user.id === action.payload.id;
            });

            if (currentUserObj) {
                currentUserObj.userOptions.seesCosts = action.payload.bool;
            }

            return {
                ...state,
                usersTab: usersTabClone
            };

        case usersTypes.SET_SELECT:

            usersTabClone = [...state.usersTab];

            currentUserObj = usersTabClone.find((userObject) => {
                return userObject.user.id === action.payload.id;
            });

            if (currentUserObj) {
                currentUserObj.userOptions.isSelected = action.payload.bool;
            }

            console.dir(currentUserObj);

            return {
                ...state,
                usersTab: usersTabClone
            };

        case usersTypes.SET_ACTIVITY:
            usersTabClone = [...state.usersTab];

            currentUserObj = usersTabClone.find((userObject) => {
                return userObject.user.id === action.payload.id;
            });

            if (currentUserObj) {
                currentUserObj.userOptions.seesActivity = action.payload.bool;
            }

            return {
                ...state,
                usersTab: usersTabClone
            };


        case usersTypes.SET_USERS:

            usersTabClone = [...state.usersTab];

            currentUserObj = usersTabClone.find((userObject) => {
                return userObject.user.id === action.payload.id;
            });

            if (currentUserObj) {
                currentUserObj.userOptions.seesUsers = action.payload.bool;
            }

            return {
                ...state,
                usersTab: usersTabClone
            };


        case usersTypes.SET_ACCESS_RIGHTS:

            return {
                ...state,
                usersTab: action.payload
            };


        case usersTypes.SET_SELECT_FOR_USERS:

            usersTabClone = [...state.usersTab];

            usersTabClone.map((i) => {
                i.userOptions.isSelected = action.payload
            });

            return {
                ...state,
                usersTab: usersTabClone
            };

        case usersTypes.SET_DAYS_LEFT:

            usersTabClone = [...state.usersTab];



            currentUserObj = usersTabClone.find((userObject) => {
                return userObject.user.id === action.payload.id;
            });

            if (currentUserObj) {
                currentUserObj.userOptions.balance = action.payload.balance;
                currentUserObj.userOptions.daysLeft = action.payload.daysValue;
            }
            console.dir(currentUserObj);
            return {
                ...state,
                usersTab: usersTabClone
            };

        case usersTypes.SET_CONFIG_LIMIT:

            usersTabClone = [...state.usersTab];



            currentUserObj = usersTabClone.find((userObject) => {
                return userObject.user.id === action.payload.id;
            });

            if (currentUserObj) {
                currentUserObj.userOptions.configsLimits.val = action.payload.configLimit;
            }
            console.dir(currentUserObj);
            return {
                ...state,
                usersTab: usersTabClone
            };




        case usersTypes.SORT_TABLE_BY_TYPE:

            usersTabClone = [...state.usersTab];


            sortTable(usersTabClone,  action.payload)

            return {
                ...state,
                usersTab: usersTabClone
            };




        case usersTypes.UPDATE_USERS:

            return {
                ...state,
                usersTab: action.payload
            };

        default:
            return state;
    }
};

export default usersReducer;
