import React from 'react';
import PersonalActivity from '../Activity/PersonalActivity';
import PersonalCost from '../Cost/PersonalCost';

import CorporateCost from './../Cost/CorporateCost';
import CorporateUsers from './../Users/CorporateUsers';
import CorporateActivity from './../Activity/CorporateActivity';
import CorporateInvite from './../Invite/CorporateInvite';
import CorporateGenerate from './../Generate/CorporateGenerate';

import './style.scss';
import {setCurrentLicense, setCurrentTab} from "../../../redux/licenses/licenses.actions";
import {connect} from "react-redux";
import LicenseAlert from './../Alerts/licenseAlert';
import GenerateAlert from './../Alerts/generateAlert'



const Dashboard = (props) => {

    let activeComponent;
    let alertWrapper = null

    if(props.licenses.currentLicense == 'Personal') {
        if(props.tab === 'activity'){
            activeComponent = (<PersonalActivity />);
        }else if(props.tab === 'costs') {
            activeComponent = (<PersonalCost />);
        }

    }else if(props.licenses.currentLicense == 'Corporate')  {

        if(props.tab === 'activity'){
            activeComponent = (<CorporateActivity />);
        }else if(props.tab === 'costs') {
            activeComponent = (<CorporateCost />);

        }else if(props.tab === 'users') {
            alertWrapper = <LicenseAlert />;
            activeComponent = (<CorporateUsers />);

            if(props.licenses.currentTab === 'Invite') {
                alertWrapper = <GenerateAlert />;
                activeComponent = (<CorporateInvite />);
            } else if(props.licenses.currentTab === 'Generate') {
                alertWrapper = <GenerateAlert />;
                activeComponent = (<CorporateGenerate />);
            }

        }

    }

    return (<>
        <div id="licenseDashboard">
            {activeComponent}
        </div>
            {alertWrapper}
        </>);
};


const mapStateToProps = (state) => ({
    licenses: state.licenses.licenses,
    currentUser: state.user.currentUser,
});

const mapDispatchToProps = (dispatch) => {
    return {
        setCurrentLicense: (license) => dispatch(setCurrentLicense(license)),
        setCurrentTab : (prop) => dispatch(setCurrentTab(prop))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Dashboard);