import {appTypes} from './app.types';

import icon_en from './icons-app-language/en.svg';
import icon_de from './icons-app-language/de.svg';
import icon_es from './icons-app-language/es.svg';
import icon_ch from './icons-app-language/ch.svg';
import icon_ru from './icons-app-language/ru.svg';

const INITIAL_STATE = {
    appState: {
        dashboard: false,

        appLanguage: {
            currentAppLanguage: 'En',
            defaultAppLanguage: 'En',
            languagesInterface: [
                {value: 'En', title: 'English', icon: icon_en},
                {value: 'De', title: 'Deutch', icon: icon_de},
                {value: 'Es', title: 'Español', icon: icon_es},
                {value: 'Ch', title: '中文', icon: icon_ch},
                {value: 'Ru', title: 'Русский', icon: icon_ru}
            ]
        },
        authModals: {
            resetPass: false,
            setNewEmail: false,
            setNewPassword: false,
            signIn: true,
            signUp: false,
        },

        modals: {
            languageDropdown: false,

            // Modals for balance  (Licenses->New License)
            buyCorpLicense: {bool: false, data: null},
            buyPersLicense: {bool: false, data: null},


            refillAccountBalance: false,
            refillLicenseBalance: false,

            changeEmail: false,
            changePass: false,
            changeTelegram: false,
            autoRenewal: false,
            migration: false,


            // Modals for action bar (Licenses->Users)
            accessRights:false,
            licenseDaysSettings: {bool: false, data: null},
            authorizationData: false,
        },
        chatState: {
            isChatOpen: false,
            currentChatId: 0
        }
    }
};

const appReducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {

        case appTypes.SET_CHAT_STATE:
            return {
                ...state,
                appState: {
                    ...state.appState,
                    chatState: {
                        ...state.appState.chatState,
                        isChatOpen: action.payload
                    }
                }
            };
        case appTypes.SET_CHAT_ID:
            return {
                ...state,
                appState: {
                    ...state.appState,
                    chatState: {
                        ...state.appState.chatState,
                        currentChatId: action.payload
                    }
                }
            };

        case appTypes.SET_DASHBOARD:
            return {
                ...state,
                appState: {
                    ...state.appState,
                    dashboard:  action.payload
                }
            };

        case appTypes.SET_CURRENT_LANGUAGE:
            return {
                ...state,
                appState: {
                    ...state.appState,
                    appLanguage: {
                        ...state.appState.appLanguage,
                        currentAppLanguage: action.payload

                    }
                }
            };

        case appTypes.SET_LANGUAGE_MODAL:
            return {
                ...state,
                appState: {
                    ...state.appState,
                    modals: {
                        ...state.appState.modals,
                        languageDropdown: action.payload
                    }
                }
            };

        case appTypes.SET_BUY_P_LICENSE_MODAL:
            return {
                ...state,
                appState: {
                    ...state.appState,
                    modals: {
                        ...state.appState.modals,
                        buyPersLicense: action.payload
                    }
                }
            };

        case appTypes.SET_BUY_C_LICENSE_MODAL:
            return {
                ...state,
                appState: {
                    ...state.appState,
                    modals: {
                        ...state.appState.modals,
                        buyCorpLicense: action.payload
                    }
                }
            };
        case appTypes.SET_REFILL_BALANCE_MODAL:
            return {
                ...state,
                appState: {
                    ...state.appState,
                    modals: {
                        ...state.appState.modals,
                        refillAccountBalance: action.payload
                    }
                }
            };

        case appTypes.SET_REFILL_LICENSE_MODAL:
            return {
                ...state,
                appState: {
                    ...state.appState,
                    modals: {
                        ...state.appState.modals,
                        refillLicenseBalance: action.payload
                    }
                }
            };

        case appTypes.SET_LICENSE_DAYS_SETTINGS_MODAL:
            return {
                ...state,
                appState: {
                    ...state.appState,
                    modals: {
                        ...state.appState.modals,
                        licenseDaysSettings: action.payload
                    }
                }
            };

        case appTypes.SET_ACCESS_RIGHTS_MODAL:
            return {
                ...state,
                appState: {
                    ...state.appState,
                    modals: {
                        ...state.appState.modals,
                        accessRights: action.payload
                    }
                }
            };

        case appTypes.SET_CHANGE_EMAIL_MODAL:
            return {
                ...state,
                appState: {
                    ...state.appState,
                    modals: {
                        ...state.appState.modals,
                        changeEmail: action.payload
                    }
                }
            };
        case appTypes.SET_CHANGE_TELEGRAM_MODAL:
            return {
                ...state,
                appState: {
                    ...state.appState,
                    modals: {
                        ...state.appState.modals,
                        changeTelegram: action.payload
                    }
                }
            };
        case appTypes.SET_CHANGE_PASSWORD_MODAL:
            return {
                ...state,
                appState: {
                    ...state.appState,
                    modals: {
                        ...state.appState.modals,
                        changePass: action.payload
                    }
                }
            };
        case appTypes.SET_AUTO_RENEWAL_MODAL:
            return {
                ...state,
                appState: {
                    ...state.appState,
                    modals: {
                        ...state.appState.modals,
                        autoRenewal: action.payload
                    }
                }
            };

        case appTypes.SET_MIGRATION_MODAL:
            return {
                ...state,
                appState: {
                    ...state.appState,
                    modals: {
                        ...state.appState.modals,
                        migration: action.payload
                    }
                }
            };


        case appTypes.SET_AUTHORIZATION_DATA_MODAL:
            return {
                ...state,
                appState: {
                    ...state.appState,
                    modals: {
                        ...state.appState.modals,
                        authorizationData: action.payload
                    }
                }
            };

            // AUTH MODALS
        case appTypes.SET_CHANGE_PASSWORD_AUTHMODAL:
            return {
                ...state,
                appState: {
                    ...state.appState,
                    authModals: {
                        ...state.appState.modals,
                        setNewPassword: action.payload
                    }
                }
            };


        case appTypes.SET_NEW_EMAIL_AUTHMODAL:
            return {
                ...state,
                appState: {
                    ...state.appState,
                    authModals: {
                        ...state.appState.modals,
                        setNewEmail: action.payload
                    }
                }
            };

        case appTypes.SET_RESET_PASS_AUTHMODAL:
            return {
                ...state,
                appState: {
                    ...state.appState,
                    authModals: {
                        ...state.appState.modals,
                        resetPass: action.payload
                    }
                }
            };

        case appTypes.SET_SIGNIN_AUTHMODAL:
            return {
                ...state,
                appState: {
                    ...state.appState,
                    authModals: {
                        ...state.appState.modals,
                        signIn: action.payload
                    }
                }
            };
        case appTypes.SET_SIGNUP_AUTHMODAL:
            return {
                ...state,
                appState: {
                    ...state.appState,
                    authModals: {
                        ...state.appState.modals,
                        signUp: action.payload
                    }
                }
            };


        default:
            return state;
    }
};

export default appReducer;
