import React from 'react';

import TableUserProfile from "../../TableComponents/UserProfile/UserProfile";
import ConfigLimits from "../../TableComponents/ConfigLimits/ConfigLimits";
import DaysLeft from "../../TableComponents/DaysLeft/DaysLeft";
import {connect} from "react-redux";
import Switch from '@material-ui/core/Switch';
import {setCosts, setUsers, setActivity, sortTableByType, setSelect} from "../../../redux/licenses/corporate/users/users.actions";
import {setCurrentLicense, setCurrentTab, setAdditionalScreenCorporate} from "../../../redux/licenses/licenses.actions";
import {setLicenseDaysSettingsModal} from "../../../redux/app/app.actions";

import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell/TableCell";
import TableBody from "@material-ui/core/TableBody";
import {IconPack} from "../../../Images/IconPack";
import InputLabel from "@material-ui/core/InputLabel/InputLabel";
import TextField from "@material-ui/core/TextField/TextField";
import Select from "@material-ui/core/Select/Select";
import MenuItem from "@material-ui/core/MenuItem";
import FormControl from "@material-ui/core/FormControl";
import Checkbox from "@material-ui/core/Checkbox/Checkbox";
import Button from "@material-ui/core/Button";


const CorporateUsers = (props) => {

    const [searchValue, setSearchValue] = React.useState();
    const [selectValue, setSelectValue] = React.useState('Date of register');
    const [orderValue, setOrderValue] = React.useState(true);

    const [editState, setEditState] = React.useState(false);
    const [wasChecked, setCheckedState] = React.useState(false);

    const handleChange = (rowData, type, event) => {

        switch (type) {
            case 'activity':
                props.setActivity(!rowData.userOptions.seesActivity, rowData.user.id);
                break;
            case 'users':
                props.setUsers(!rowData.userOptions.seesUsers, rowData.user.id);
                break;
            case 'costs':
                props.setCosts(!rowData.userOptions.seesCosts, rowData.user.id);
                break;
            default:
                break;

        }
    };

    let selectedDataClone = [];
     props.usersTabData.map((i)=> {
        if(i.userOptions.isSelected) {
            selectedDataClone.push(i);
        }
    });


    const [tableSort, setSort] = React.useState('date of registration');

    const handleSort = (event) => {
        setSort(event.target.value);

        if (event.target.value === 'date of registration') {
            props.sortTableByType('byRegistration');

        } else if (event.target.value === 'date of last visit') {
            props.sortTableByType('byVisit');
        }
    };


    const tableCheckBox = (row) => {
        return (<Checkbox color="primary"  checked={row.userOptions.isSelected} onChange={()=> {
            props.setSelect(!row.userOptions.isSelected, row.user.id);
            setCheckedState(true);
            }}/>);
    }


    const tableItems = (rowData) => {
        return (<TableRow className={rowData.userOptions.isSelected ? 'active' : null}>

            <TableCell className="padding-sides-0">
                <div>
                { editState && ( (wasChecked && selectedDataClone.length > 0) || (!wasChecked && selectedDataClone.length === 0)  )  ? tableCheckBox(rowData) : null}
                <TableUserProfile dataImage={rowData.user.image} dataName={rowData.user.name} dataId={rowData.user.id}/>
                </div>
            </TableCell>

            <TableCell className="padding-sides-5">{rowData.user.telegram}</TableCell>
            <TableCell className="padding-sides-20 white-space-normal">{rowData.user.comment}</TableCell>

            <TableCell className="padding-sides-20">
                <Switch checked={rowData.userOptions.seesActivity} onChange={(e) => {
                    handleChange(rowData, 'activity', e)
                }}/></TableCell>

            <TableCell className="padding-sides-25"> <Switch checked={rowData.userOptions.seesUsers} onChange={(e) => {
                handleChange(rowData, 'users', e)
            }} n/></TableCell>

            <TableCell className="padding-sides-25"> <Switch checked={rowData.userOptions.seesCosts} onChange={(e) => {
                handleChange(rowData, 'costs', e)
            }} /></TableCell>

            <TableCell className="padding-sides-25"><DaysLeft rowData={rowData}/></TableCell>
            <TableCell className="padding-sides-0 va-top"><ConfigLimits dataValue={rowData}/>

            <div className="btn--pins">
                {IconPack.buttons.pins}
            </div>

            </TableCell>
        </TableRow>)
    };


    return (
        <div className="сustomStyle">
            <div className="filterPanel">

                <div className="position-relative w100percent">
                    <TextField
                        variant="filled"
                        fullWidth
                        className="search_area"
                        placeholder="Search"
                        value={searchValue}
                        onChange={(e) => {
                            setSearchValue(e.target.value)
                        }}
                    >
                    </TextField>
                    <div className="search_additional_components">
                        <div>{IconPack.searchComponents.filter}</div>
                    </div>
                </div>

                <FormControl variant="filled" className='filterPanel__select w160 mr10'>
                    <TextField
                        labelId="selectlabel"
                        value={selectValue}
                        label='Sort users by'
                        select
                        variant="filled"
                        onChange={(e) => {
                            setSelectValue(e.target.value)
                        }}
                        >
                        <MenuItem key={1} value={'Date of register'}>Date of register</MenuItem>
                        <MenuItem key={2} value={'Date of last visit'}>Date of last visit</MenuItem>
                    </TextField>
                </FormControl>
                <div className="filterPanel__select__order" onClick={()=>{setOrderValue(!orderValue)}}>
                    { orderValue ? IconPack.searchComponents.orderDown : IconPack.searchComponents.orderDown }
                </div>

                <div className="filterPanel__edit" onClick={()=>{setEditState(true);  setCheckedState(false);}}>
                    {IconPack.searchComponents.edit}
                </div>


                <Button variant="contained" className="btn btn--blue filterPanel__add"  onClick={()=>{
                    props.setCurrentTab('Generate');
                    props.setAdditionalScreenCorporate(true);
                }}>
                    {IconPack.searchComponents.add}
                </Button>

            </div>
            <Table className="licenseUsersTable">
                <TableHead>
                    <TableRow className="doubled-row">
                        <TableCell className="padding-sides-15 w200">Nickname and Login</TableCell>
                        <TableCell className="padding-sides-5 w100">Telegram</TableCell>
                        <TableCell className="padding-sides-20">Comment</TableCell>
                        <TableCell className="padding-sides-25 w75">Sees <br />activity</TableCell>
                        <TableCell className="padding-sides-25 w75">Sees <br />users</TableCell>
                        <TableCell className="padding-sides-25 w75">Sees <br />costs</TableCell>
                        <TableCell className="padding-sides-25 w170">Days of using <br /> browser left </TableCell>
                        <TableCell className="padding-sides-0 w215">Configs limits <br />in Config Shop</TableCell>
                    </TableRow>
                </TableHead>
                <TableBody>

                    {props.usersTabData.map((rowData) => {
                        return tableItems(rowData);
                    })}


                </TableBody>
            </Table>
        </div>
    );
}


const mapStateToProps = (state) => ({
    usersTabData: state.corporateLicenseUsers.usersTab,
});

const mapDispatchToProps = (dispatch) => {
    return {
        setCosts: (bool, id) => dispatch(setCosts(bool, id)),
        setUsers: (bool, id) => dispatch(setUsers(bool, id)),
        setActivity: (bool, id) => dispatch(setActivity(bool, id)),
        setSelect: (bool, id) => dispatch(setSelect(bool, id)),
        setCurrentTab: (prop) => dispatch(setCurrentTab(prop)),
        setAdditionalScreenCorporate: (bool) => dispatch(setAdditionalScreenCorporate(bool)),
        sortTableByType: (type) => dispatch(sortTableByType(type)),
        setLicenseDaysSettingsModal: (type) => dispatch(setLicenseDaysSettingsModal(type)),
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(CorporateUsers);